require('normalize.css/normalize.css')
require('amazeui/dist/css/amazeui.min.css')
require('styles/App.css')
import React from 'react'
import ReactDOM from 'react-dom'
import { HashRouter, Route } from 'react-router-dom'
import { Header, Menu ,Footer} from 'amazeui-react'
import myHome from './pages/home'
import cook from './pages/cook'
import aboutUs from './pages/about'
import classification from './pages/classification'
export default class AppComponent extends React.Component {
	constructor(props) {
		super(props)
		this.state = {
			menuData: [{
				link: '#/',
				title: ''
			}, {
				link: '#/classification',
				title: ''
			}, {
				link: '#/cook',
				title: ''
			}, {
				link: '#/aboutUs',
				title: ''
			}],
			footerData: [
  'CopyRight2014 AllMobilize Inc.',
  'ICP13033158'
]
		}
//		this.handleClick = this.handleClick.bind(this);
	}
//	handleClick(nav, index, e) {
//		if(nav && nav.subMenu) {
//			// 
//		} else {
//			e.preventDefault();
//			console.log('', nav);
//			// do something
//			// this.handleToggle(); // 
//		}
//	};
	render() {
		/*========================================*/
		return(
			<HashRouter>
   			<div>
   			 <Header title="Menu Demo" />
    <Menu
      toggleIcon="list"
      data={this.state.menuData}
      theme="dropdown1"
       />
  			<div>{this.props.children}</div>
       		 <Route exact path='/' component={myHome} />
        	<Route path='/cook' component={cook} />
        	<Route path='/aboutUs' component={aboutUs}/>
        	<Route path='/classification' component={classification} />
        	  			<Footer data={this.state.footerData} mobileTitle="" className='footer-box' />
        </div>

  </HashRouter>
		)
	}
}
ReactDOM.render((
	<AppComponent/>
), document.getElementById('app'));


